#display {
  position: absolute;
  top: 13.75rem;
  right: 0;
  background-color: var(--bg-color-main);
  width: calc(100vw - 18.75rem);
  height: calc(100vh - 13.75rem);
}

#display__canvas {
  position: absolute;
  bottom: 0;
  right: 0;
  border-radius: 15px 0 0 0;
  box-shadow: -1px -1px 10px -0.5px rgba(0, 0, 0, 1);
  background-color: white;
  width: calc(100vw - 19.5rem);
  height: calc(100vh - 14.4rem);
  overflow-y: scroll;
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: space-evenly;
}

.card {
  position: relative;
  background-color: var(--color-secondary);
  border: 1px solid var(--color-secondary);
  width: 25rem;
  height: 15rem;
  border-radius: 15px;
  transition-duration: 0.3s;
  overflow: hidden;
}

.card:hover {
  cursor: pointer;
  transform: scale(105%);
  box-shadow: 1px 0px 10px -1px rgba(0, 0, 0, 1);
}

.display__img {
  width: 25rem;
  height: 15rem;
  object-fit: cover;
}
