@import "https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap";
:root {
  --bg-color-main: #ececec;
  --color-main: #3e3e3e;
  --color-secondary: #656565;
  --color-white: #fff;
  --color-black: #000;
}

.page-header {
  width: 100vw;
  height: 10rem;
  background-image: url("Rectangle-53.ac23ad4b.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.page-header__navbar {
  width: 100%;
  height: 3.75rem;
  background-color: var(--bg-color-main);
  justify-content: center;
  align-items: center;
  display: flex;
}

.page-header__logo {
  width: 3.125rem;
  height: 1.875rem;
  fill: var(--color-main);
  stroke: var(--color-main);
}

.profile-container {
  width: 18.75rem;
  height: calc(100vh - 13.75rem);
  background-color: var(--color-white);
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 1px 0 10px -1px #000;
}

.profile__img-container {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  margin-top: 1.875rem;
  position: relative;
  overflow: hidden;
}

.profile__img {
  width: 11rem;
  height: auto;
  object-fit: contain;
  position: absolute;
  right: -15px;
}

.profile__name-box, .profile__contact-box {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.name {
  color: var(--color-main);
  margin-top: 1.875rem;
  font-size: 24px;
  font-weight: 500;
  line-height: 21.6px;
}

.job {
  color: var(--color-secondary);
  margin-top: .625rem;
  font-size: 16px;
  font-weight: 500;
  line-height: 14.4px;
}

.profile__contact-box {
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  margin-top: 3.75rem;
  display: flex;
}

.contact__link {
  align-items: center;
  gap: 5px;
  text-decoration: none;
  display: flex;
}

.contact__text {
  color: var(--color-secondary);
  font-size: 1rem;
  font-weight: 500;
  line-height: 14.4px;
}

.logo {
  height: 1.5rem;
  width: 1.5rem;
  fill: var(--color-secondary);
  stroke: var(--color-secondary);
}

.location {
  color: var(--color-secondary);
  font-size: 14px;
  font-weight: 500;
  line-height: 12.6px;
  position: absolute;
  bottom: 1.25rem;
  left: auto;
}

.location p {
  align-items: center;
  display: flex;
}

#display {
  background-color: var(--bg-color-main);
  width: calc(100vw - 18.75rem);
  height: calc(100vh - 13.75rem);
  position: absolute;
  top: 13.75rem;
  right: 0;
}

#display__canvas {
  width: calc(100vw - 19.5rem);
  height: calc(100vh - 14.4rem);
  background-color: #fff;
  border-radius: 15px 0 0;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 2rem;
  padding: 1rem;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 0;
  overflow-y: scroll;
  box-shadow: -1px -1px 10px -.5px #000;
}

.card {
  background-color: var(--color-secondary);
  border: 1px solid var(--color-secondary);
  width: 25rem;
  height: 15rem;
  border-radius: 15px;
  transition-duration: .3s;
  position: relative;
  overflow: hidden;
}

.card:hover {
  cursor: pointer;
  transform: scale(105%);
  box-shadow: 1px 0 10px -1px #000;
}

.display__img {
  width: 25rem;
  height: 15rem;
  object-fit: cover;
}

.modal__container {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, .7);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.modal {
  width: 31.25rem;
  height: 39rem;
  background-color: #fff;
  border-radius: 15px;
  flex-flow: column;
  align-items: center;
  display: flex;
  position: relative;
}

.modal__close-btn {
  width: 2rem;
  height: 2rem;
  background-color: var(--bg-color-main);
  z-index: 99;
  border: hidden;
  border-radius: 25px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__close-btn:hover {
  cursor: pointer;
  background-color: var(--color-secondary);
}

.modal__img-container {
  width: 90%;
  height: 45%;
  background-color: gray;
  border-radius: 15px;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
}

.modal__img {
  width: 39.8rem;
  height: auto;
  position: absolute;
  left: -5.6rem;
}

.modal__text-container {
  width: 90%;
  flex-flow: column;
  align-items: flex-start;
  display: flex;
}

.modal__title {
  margin-top: 30px;
}

.modal__label {
  color: var(--color-black);
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: 700;
}

.modal__text {
  color: var(--color-main);
  font-size: 18px;
  font-weight: 500;
}

.hidden {
  display: none;
}

* {
  box-sizing: border-box;
  font-optical-sizing: auto;
  margin: 0;
  font-family: Inter, sans-serif;
}

